import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { TreeComponent, TreeNode, ITreeOptions } from 'angular-tree-component';
import { SplitComponent } from 'angular-split';
import openapi_doc from "../assets/resourcemanager.json";
const yaml = require('js-yaml')

export interface ITreeNode {
    name: string;
    id: number;
    path: string;
    children: ITreeNode[];
    hasChildren?: boolean;
    // docType: eDocType;
    // doc: IClassNode | IPropertyNode | IMethodNode;
}

enum Views {
    openapi,
    ixnetwork_restpy,
    python_requests
};

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent {
    @ViewChild('splitter', { static: false }) _splitterComponent: SplitComponent;
    @ViewChild('tree', { static: false }) _treeComponent: TreeComponent;
    @ViewChild('editor', { static: false }) editor;
    private _openapi_doc: any;
    private _treeOptions: ITreeOptions;
    private _nextId: number = 1;
    private _nodes: any;
    private _sample: any;
    private _view: Views = Views.openapi;

    constructor() {
        this._openapi_doc = openapi_doc;
        this._treeOptions = {
            getChildren: this.getChildren.bind(this),
            animateSpeed: 0,
            animateAcceleration: 0,
            animateExpand: false,
            useVirtualScroll: false,
            useCheckbox: false,
            useTriState: false
        };
        this._nodes = this.getNodes('#/components/schemas/ixnetwork');
    }

    initEditor() {
        this.editor.setOptions({
            // editor options
            selectionStyle: 'line', // "line"|"text"
            highlightActiveLine: false, // boolean
            highlightSelectedWord: true, // boolean
            readOnly: true, // boolean: true if read only
            cursorStyle: 'ace', // "ace"|"slim"|"smooth"|"wide"
            mergeUndoDeltas: true, // false|true|"always"
            behavioursEnabled: true, // boolean: true if enable custom behaviours
            wrapBehavioursEnabled: true, // boolean
            autoScrollEditorIntoView: undefined, // boolean: this is needed if editor is inside scrollable page
            keyboardHandler: null, // function: handle custom keyboard events

            // renderer options
            animatedScroll: false, // boolean: true if scroll should be animated
            displayIndentGuides: false, // boolean: true if the indent should be shown. See 'showInvisibles'
            showInvisibles: false, // boolean -> displayIndentGuides: true if show the invisible tabs/spaces in indents
            showPrintMargin: false, // boolean: true if show the vertical print margin
            printMarginColumn: 80, // number: number of columns for vertical print margin
            printMargin: undefined, // boolean | number: showPrintMargin | printMarginColumn
            showGutter: true, // boolean: true if show line gutter
            fadeFoldWidgets: false, // boolean: true if the fold lines should be faded
            showFoldWidgets: true, // boolean: true if the fold lines should be shown ?
            showLineNumbers: true,
            highlightGutterLine: false, // boolean: true if the gutter line should be highlighted
            hScrollBarAlwaysVisible: false, // boolean: true if the horizontal scroll bar should be shown regardless
            vScrollBarAlwaysVisible: false, // boolean: true if the vertical scroll bar should be shown regardless
            fontSize: 16, // number | string: set the font size to this many pixels
            fontFamily: undefined, // string: set the font-family css value
            maxLines: undefined, // number: set the maximum lines possible. This will make the editor height changes
            minLines: undefined, // number: set the minimum lines possible. This will make the editor height changes
            maxPixelHeight: 0, // number -> maxLines: set the maximum height in pixel, when 'maxLines' is defined. 
            scrollPastEnd: 0, // number -> !maxLines: if positive, user can scroll pass the last line and go n * editorHeight more distance 
            fixedWidthGutter: false, // boolean: true if the gutter should be fixed width
            theme: 'ace/theme/eclipse', // theme string from ace/theme or custom?

            // mouseHandler options
            scrollSpeed: 2, // number: the scroll speed index
            dragDelay: 0, // number: the drag delay before drag starts. it's 150ms for mac by default 
            dragEnabled: true, // boolean: enable dragging
            focusTimeout: 0, // number: the focus delay before focus starts.
            tooltipFollowsMouse: true, // boolean: true if the gutter tooltip should follow mouse

            // session options
            firstLineNumber: 1, // number: the line number in first line
            overwrite: false, // boolean
            newLineMode: 'auto', // "auto" | "unix" | "windows"
            useWorker: false, // boolean: true if use web worker for loading scripts
            useSoftTabs: true, // boolean: true if we want to use spaces than tabs
            tabSize: 4, // number
            // tslint:disable-next-line:max-line-length
            wrap: true, // boolean | string | number: true/'free' means wrap instead of horizontal scroll, false/'off' means horizontal scroll instead of wrap, and number means number of column before wrap. -1 means wrap at print margin
            indentedSoftWrap: true, // boolean
            foldStyle: 'markbegin', // enum: 'manual'/'markbegin'/'markbeginend'.
            mode: 'ace/mode/yaml' // string: path to language mode 
        });
    }

    getChildren(node: any): Promise<any[]> {
        let children: any[] = this.getNodes(node.data.ref);
        return new Promise<any[]>((resolve, reject) => {
            setTimeout(() => { resolve(children); }, 0);
        });
    }

    get options(): ITreeOptions {
        return this._treeOptions;
    }

    ngOnInit() {
    }

    ngOnDestroy() {
    }

    ngAfterViewInit() {
        this._splitterComponent.direction = 'horizontal';
        this._splitterComponent.disabled = false;
        this.initEditor();
        setTimeout(() => this._treeComponent.treeModel.getFirstRoot().setActiveAndVisible());
    }

    get nextId(): number {
        return this._nextId++;
    }

    public onActivate(event: any) {
        if (event.node) {
            switch (this._view) {
                case Views.openapi:
                    this.onOpenApi(null);
                    break;
                case Views.ixnetwork_restpy:
                    this.onRestpy(null);
                    break;
                case Views.python_requests:
                    this.onRequests(null);
                    break;
            }
        }
        // this._cdref.detectChanges();
    }

    public get nodes(): any {
        return this._nodes;
    }

    getNode(path: string): any {
        let currentNode = this._openapi_doc;
        for (let piece of path.slice(2).split('/')) {
            currentNode = currentNode[piece];
        }
        return currentNode;
    }

    getNodes(path: string): any {
        let currentNode = this.getNode(path);
        let nodes = [];
        if (Object.keys(currentNode).indexOf('properties') === -1) {
            return;
        }
        for (let name in currentNode.properties) {
            if (name !== 'xpath') {
                let node = currentNode.properties[name];
                let ref = null;
                if (node['$ref']) {
                    ref = node['$ref']
                    node = this.getNode(ref);
                } else if (node['items'] && node['items']['$ref']) {
                    ref = node['items']['$ref']
                    node = this.getNode(ref)
                } else {
                    continue;
                }
                let hasChildren: boolean = false;
                for (let key in node.properties) {
                    if (key === 'xpath') {
                        continue;
                    }
                    if(node.properties[key]['$ref'] || node.properties[key].type == 'object' || node.properties[key].type == 'array') {
                        hasChildren = true;
                        break;
                    }
                }
                let data = {
                    id: this.nextId,
                    path: `${path}.properties.${name}`,
                    ref: ref,
                    multiples: node['x-crud'].indexOf('create') !== -1,
                    name: name,
                    hasChildren: hasChildren
                };
                if (!hasChildren) {
                    data['children'] = [];
                }
                nodes.push(data);
            }
        }
        return nodes;
    }

    public onDownload(event: any) {
        window.open('./assets/resourcemanager.yaml', '_blank');
    }

    public get sample(): any {
        return this._sample;
    }

    public onOpenApi(event: any) {
        if (event) {
            this._view = Views.openapi;
        }
        let node = this._treeComponent.treeModel.focusedNode;
        this.editor.setMode('ace/mode/yaml');
        let yamlNode = this.getNode(node.data.ref);
        this._sample = `OpenAPI Object Schema\n`;
        this._sample += `resourcemanager.yaml${node.data.ref}\n\n`;
        this._sample += node.data.name + ':\n';
        for (let line of yaml.dump(yamlNode).split('\n')) {
            let xpath = line.indexOf(`$ref: '#/components/schemas/xpath'`);
            if (xpath !== -1) {
                // let xpathNode = this.getNode(line.slice(xpath + 7, -1));
                this._sample += '      ' + 'type: string\n';
                this._sample += '      ' + 'format: xpath\n';
                this._sample += '      ' + `example: ${this.getXpathExample(node)}\n`;
            } else {
                this._sample += '  ' + line + '\n';
            }
        }
    }

    getXpathExample(node: any): string {
        let nameNode = node;
        let xpathExample = '';
        while (nameNode.data.name) {
            let index = '';
            if (nameNode.data.multiples) {
                index = '[1]';
            }
            xpathExample = `/${nameNode.data.name}${index}${xpathExample}`;
            nameNode = nameNode.parent;
        }
        return xpathExample;
    }

    public onGo(): string {
        return `
package main

import (
    "json"
    "net/http"
)

func main() {
    vport := []byte(\`{"name":"","type":"ethernet"}\`)
    url := "http://127.0.0.1:11009/api/v1/sessions/1/ixnetwork/resourceManager/operations/importConfig"
    res, err := http.Post(url, "application/json", vport.New)
}
        `;
    }

    public onRestpy(event: any) {
        if (event) {
            this._view = Views.ixnetwork_restpy;
        }
        let node = this._treeComponent.treeModel.focusedNode;
        this.editor.setMode('ace/mode/python');
        let xpathExample = this.getXpathExample(node);
        this._sample = `"""ixnetwork-restpy\n\n`;
        this._sample += `\tSchema object source is resourcemanager.yaml${node.data.ref}\n\n`;
        this._sample += `\tthe importConfig API uses the xpath property in each object to lookup a resource on the server\n`;
        this._sample += `\tIF the ${node.data.name} resource exists\n`;
        this._sample += `\t\tthe existing resource will be updated\n`;
        this._sample += `\tELSE IF create is defined in the x-crud property\n`;
        this._sample += `\t\ta new ${node.data.name} resource will be created\n`;
        this._sample += `\tELSE\n`;
        this._sample += `\t\tan errata item will be returned\n`;
        this._sample += '"""\n';
        this._sample += 'import json\n';
        this._sample += 'from ixnetwork_restpy import SessionAssistant\n';
        this._sample += '\n';
        this._sample += `session = SessionAssistant(IpAddress='127.0.0.1')\n`;
        let schema = this.getNode(node.data.ref);
        this._sample += `${node.data.name} = {\n`;
        this._sample += `\t'xpath': '${xpathExample}',\n`;
        for (let key in schema.properties) {
            let property = schema.properties[key];
            switch (property.type) {
                case "string":
                    this._sample += `\t'${key}': '',\n`;
                    break;
                case "boolean":
                    this._sample += `\t'${key}': 'true',\n`;
                    break;
                case "enum":
                    this._sample += `\t'${key}': '',\n`;
                    break;
                case "number":
                    this._sample += `\t'${key}': 0,\n`;
                    break;
                }
        }
        this._sample += '}\n';
        this._sample += `session.Ixnetwork.ResourceManager.ImportConfig(json.dumps(${node.data.name}), True)`;
    }

    public onRequests(event: any) {
        if (event) {
            this._view = Views.python_requests;
        }
        let node = this._treeComponent.treeModel.focusedNode;
        this.editor.setMode('ace/mode/python');
        let xpathExample = this.getXpathExample(node);
        this._sample = `"""python requests\n\n`;
        this._sample += `\tSchema object source is resourcemanager.yaml${node.data.ref}\n\n`;
        this._sample += `\tthe importConfig API uses the xpath property in each object to lookup a resource on the server\n`;
        this._sample += `\tIF the ${node.data.name} resource exists\n`;
        this._sample += `\t\tthe existing resource will be updated\n`;
        this._sample += `\tELSE IF create is defined in the x-crud property\n`;
        this._sample += `\t\ta new ${node.data.name} resource will be created\n`;
        this._sample += `\tELSE\n`;
        this._sample += `\t\tan errata item will be returned\n`;
        this._sample += '"""\n';
        this._sample += 'import requests, json\n';
        this._sample += '\n';
        let schema = this.getNode(node.data.ref);
        this._sample += `${node.data.name} = {\n`;
        this._sample += `\t'xpath': '${xpathExample}',\n`;
        for (let key in schema.properties) {
            let property = schema.properties[key];
            switch (property.type) {
                case "string":
                    this._sample += `\t'${key}': '',\n`;
                    break;
                case "boolean":
                    this._sample += `\t'${key}': 'true',\n`;
                    break;
                case "enum":
                    this._sample += `\t'${key}': '',\n`;
                    break;
                case "number":
                    this._sample += `\t'${key}': 0,\n`;
                    break;
                case "object":
                    break;
            }
        }
        this._sample += '}\n';
        this._sample += `payload = {\n`;
        this._sample += `\t'arg1': '/resourceManager',\n`;
        this._sample += `\t'arg2': json.dumps(${node.data.name}),\n`;
        this._sample += `\t'arg3': True\n`;
        this._sample += `}\n`;
        this._sample += `url = 'http://127.0.0.1:11009/api/v1/sessions/1/ixnetwork/resourceManager/operations/importConfig'\n`;
        this._sample += `response = requests.request('POST', url, json=payload)\n`;
    }
}
